import React, { Component } from 'react'
import { Tree } from 'antd';
const TreeNode = Tree.TreeNode;

const data = require('./subject.data').default;
const myData = data.filter(({ user_id }) => user_id === 'skytale');

const getSubjectData = (list) => {
  const subject = list.
    filter(({ object_type }) => object_type === 'SUBJECT')
    .map(({ prvt_proxy_id, name }) => ({ title: name, key: prvt_proxy_id }))
  console.log(subject)

  list.
    filter(({ object_type }) => object_type === 'MODEL')
    .map(({ prvt_proxy_id, name, up_prvt_proxy_id }) => ({ title: name, key: prvt_proxy_id, parent: up_prvt_proxy_id }))
    .forEach(obj => {
      let parentIndex = subject.findIndex(({ key }) => key === obj.parent);
      let parent = parentIndex > -1 ? subject[parentIndex] : null;
      if (parent){
        parent.children = parent.children || [];
        parent.children.push(obj);
      }
    })
  return subject;
}

const model = getSubjectData(myData);

class Subject extends Component {
  state = {
    model
  }
  onDragEnter = (info) => {
    console.log(info);
  }
  onDrop = (info) => {
    console.log(info);
    const dropKey = info.node.props.eventKey;
    const dragKey = info.dragNode.props.eventKey;
    const dropPos = info.node.props.pos.split('-');
    const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1]);
    // const dragNodesKeys = info.dragNodesKeys;
    const loop = (data, key, callback) => {
      data.forEach((item, index, arr) => {
        if (item.key === key) {
          return callback(item, index, arr);
        }
        if (item.children) {
          return loop(item.children, key, callback);
        }
      });
    };
    const data = [...this.state.gData];
    let dragObj;
    loop(data, dragKey, (item, index, arr) => {
      arr.splice(index, 1);
      dragObj = item;
    });
    if (info.dropToGap) {
      let ar;
      let i;
      loop(data, dropKey, (item, index, arr) => {
        ar = arr;
        i = index;
      });
      if (dropPosition === -1) {
        ar.splice(i, 0, dragObj);
      } else {
        ar.splice(i + 1, 0, dragObj);
      }
    } else {
      loop(data, dropKey, (item) => {
        item.children = item.children || [];
        item.children.push(dragObj);
      });
    }
    this.setState({
      gData: data,
    });
  }
  render() {
    const loop = data => data.map((item) => {
      if (item.children && item.children.length) {
        return <TreeNode key={item.key} title={item.title}>{loop(item.children)}</TreeNode>;
      }
      return <TreeNode key={item.key} title={item.title} />;
    });
    return (
      <Tree
        className="draggable-tree"
//        defaultExpandedKeys={this.state.expandedKeys}
        //draggable
        //onDragEnter={this.onDragEnter}
        //onDrop={this.onDrop}
      >
        {loop(this.state.model)}
      </Tree>
    );
  }
}

export default Subject;


